import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { CustomValidators } from 'ng2-validation';
import swal from 'sweetalert2';
import { GetChemistDataService } from '../../services/GetChemistDataService';
import { AddAreaService } from '../../services/AddAreaService';
import { GetPopularChemistService } from '../../services/GetPopularChemistService';
import { AddchemistService } from '../../services/AddChemistService';
import { AddCategoryService } from '../../services/AddCategoryService';
import { MeasurementAndDosageSizesService } from './../../services/MeasurementAndDosageSizesService';
import { AddMedicineService } from './../../services/AddMedicineService';

import { ValidationModel } from './../../models/validation.class';
import { DatatableComponent } from '@swimlane/ngx-datatable';
import { UserService } from '../../services/userService';



declare var jQuery: any;

@Component({
  selector: 'add-medicine',
  templateUrl: './add-medicine.component.html',
  styleUrls: [
    './add-medicine.component.scss',
    '../../../../assets/icon/icofont/css/icofont.scss'
  ],
  providers: [UserService, AddMedicineService, GetChemistDataService, AddAreaService, AddCategoryService, GetPopularChemistService, AddchemistService, MeasurementAndDosageSizesService]
})
export class AddMedicineComponent implements OnInit {

  @ViewChild(DatatableComponent) table: DatatableComponent;
  @ViewChild(DatatableComponent) table2: DatatableComponent;

  /**
   * End
   */
  public MedicineName: string = "";
  public Address: string = "";
  public ManufacturerName: string = "";
  public PharmaCommission: number = 0;
  public City: number = 0;
  public Area: number = 0;
  public PharmacistId: number = 0;
  public SubCategoryId: number = 0;
  public CategoryId: number = 0;
  public ImageUrl: string = "";
  public DosageId: number = 0;
  public DosageSizeId: number = 0;
  public MeasurementId: number = 0;
  public DosageValueId: number = 0;
  public ProductPrice: number = 0;
  public ProductQuantity: number = 0;
  public Username: string = "";
  public Password: string = "";
  public Latitude: number = 0;
  public Longitude: number = 0;
  public ManufacturerId: number = 0;

  public cities_array = [];
  public areas_array = [];
  public chemists_array = [];
  public chemists_categories = [];
  public chemists_sub_categories = [];
  public measurements_array = [];
  public dosages_array = [];
  public manufacturers_array = [];
  public dosages_sizes_array = [];





  public MeasurementListId: number = 0;
  public measurement_final_ids = [];
  public variation_array = [];
  public variation_model = {
    Id: 0,
    measurement_id: 0,
    measurement_name: "",
    dosage_id: 0,
    dosage_name: "",
    dosage_value_id: 0,
    dosage_value_name: "",
    quantity: 0,
    price: 0
  }
  public measurement_model = {
    Id: 0
  }

  public runner: boolean = false;

  timeout: any;

  rowsBasic = [];
  fullScreenRow = [];
  loadingIndicator = true;
  reorderable = true;
  DosageSize = {};
  DosageSizeee: any = [];

  columns = [
    { prop: 'name' },
    { name: 'Gender' },
    { name: 'Company' }
  ];

  rows = [];
  rowsFilter = [];
  tempFilter = [];

  rowsFilter2 = [];
  tempFilter2 = [];

  public ProfilePicture = File;
  public LogoUrl: any;

  public showAttr: boolean = false;



  /* Osamas's code */

  public pharmacist_id_error: boolean = false;
  public medicine_name_error: boolean = false;
  public manufacturer_name_error: boolean = false;
  public category_id_error: boolean = false;
  public sub_category_id_error: boolean = false;
  public image_error: boolean = false;
  public measurement_id_error: boolean = false;
  public dosage_id_error: boolean = false;
  public dosage_value_id_error: boolean = false;
  public product_quantity_error: boolean = false;
  public product_price_error: boolean = false;
  public ShowMedicineForm: boolean = false;
  public measurement_list_error: boolean = false;
  public variation_list_error: boolean = false;
  public runnerLoader: boolean = false;

  public measurementIdArray = [];



  constructor(public _getChemistDataService: GetChemistDataService,
    public _getPopularChemistService: GetPopularChemistService,
    public _AddAreaNameService: AddAreaService,
    public _addChemistServiceService: AddchemistService,
    public _addCategoryService: AddCategoryService,
    public _userService: UserService,
    public _addMeasurementServices: MeasurementAndDosageSizesService,
    public _addMedicineService: AddMedicineService) {

    var session_obj = JSON.parse(localStorage.getItem('session_obj'));
    if (session_obj.UserRole == 1) {
      this.getChemists();
      this.showAttr = true;
    }
    else if (session_obj.UserRole == 2) {
      this.PharmacistId = session_obj.ChemistId;
      this.showAttr = false;
      this.getCategories();
    }
    this.getMeasurements();
    this.getDosages();
    this.getManufacturers();
  }

  ngOnInit(): void {


    this._addMeasurementServices.getMeasurements().subscribe(response => {
      if (response.code == 200) {
        console.log(response);
        this.selectedMeasurementArry = response.data;
      }
    });

  }
  getDosages() {
    this._addMeasurementServices.getDosage().subscribe(response => {
      if (response.code == 200) {
        this.dosages_array = response.data;
      }
    });

  }// Get Dosages End

  getDosageValues() {
    this._addMeasurementServices.getDosageSizes(this.DosageId).subscribe(response => {
      if (response.code == 200) {
        this.dosages_sizes_array = response.data;
      }
    });
  }// Get Dosages End

  getManufacturers() {
    this._addMeasurementServices.getAllManufactures().subscribe(response => {
      if (response.code == 200) {

        this.manufacturers_array = response.data;
      }
    });
  }// Get Dosages End

  getMeasurements() {
    this._addMeasurementServices.getMeasurements().subscribe(response => {
      if (response.code == 200) {
        this.measurements_array = response.data;
      }
    });
  }// Get Measurements End

  getCategories() {
    this._addCategoryService.getCateogry(this.PharmacistId).subscribe(
      response => {
        this.chemists_categories = response.data;
      });
  }// End of Get Categories

  getSubCategories() {
    this._addCategoryService.getAllSubCategoryValues(this.PharmacistId, this.CategoryId).subscribe(
      response => {
        this.chemists_sub_categories = response.data;
      });
  }// End of Get Sub Categories
  getChemists() {
    this._getChemistDataService.getRegisteredChemistService().subscribe(
      response => {

        for (var i = 0; i < response.data.length; i++) {
          this.chemists_array.push(response.data[i].Chemist);
        }

      });
  }// End of Get Chemists

  resetErrors() {
    this.pharmacist_id_error = false;
    this.medicine_name_error = false;
    this.manufacturer_name_error = false;
    this.category_id_error = false;
    this.sub_category_id_error = false;
    this.measurement_list_error = false;
    this.variation_list_error = false;
    this.image_error = false;
  }

  public measurement_ids = [];

  addMedicine() {


    


    var error = false;
    this.resetErrors();


    


    if (ValidationModel.validateNumber(this.PharmacistId) || this.PharmacistId == 0) {

      error = true;
      this.pharmacist_id_error = true;

    }

    if (ValidationModel.validateString(this.MedicineName)) {

      error = true;
      this.medicine_name_error = true;

    }


    if (this.ManufacturerId == undefined || this.ManufacturerId == 0) {

      error = true;
      this.manufacturer_name_error = true;


    }
    if (this.CategoryId == undefined || this.CategoryId == 0) {

      error = true;
      this.category_id_error = true;

    }
    if (this.SubCategoryId == undefined || this.SubCategoryId == 0) {

      error = true;
      this.sub_category_id_error = true;

    }
    if (this.variation_array.length == 0) {

      error = true;
      this.variation_list_error = true;
    }
       if (this.measurementIdArray.length == 0) {
        error = true;
        this.measurement_list_error = true;
  
      } 


    if(this.LogoUrl == undefined){
      error = true;
      this.image_error =true;
    }

      


    if (error) {

    } else {
      this.runnerLoader = true;

     
      
      
      var variation_dto_array = [];
      for (var i = 0; i < this.variation_array.length; i++) {
        var obj = {
          Quantity: this.variation_array[i].quantity,
          Price: this.variation_array[i].price,
          DosageAndSizesValuesId: this.variation_array[i].dosage_value_id,
          MeasurementId: this.variation_array[i].measurement_id
        }
        variation_dto_array.push(obj);
      }
    

   this._addMedicineService.addMedicine(this.MedicineName, this.ManufacturerId,
        this.measurementIdArray, variation_dto_array, this.SubCategoryId, this.PharmacistId,
        this.LogoUrl).subscribe(response => {


          $('#loader').hide();

          console.log(response);

          if (response.code == 200) {
            this.runnerLoader = false;
            swal(response.message, "Medicine added Successfully", "success");
            $("#blah").attr("src", "../../../../assets/images/pharma-avatar.png");
            this.MedicineName = "";
            this.ManufacturerName = "";
            this.PharmacistId = 0;
            this.ImageUrl = "";
            this.CategoryId = 0;
            this.SubCategoryId = 0;
            this.MeasurementListId = 0;
            this.ManufacturerId = 0;
            this.MeasurementId = 0;
            this.DosageId = 0;
            this.DosageValueId = 0;
            this.ProductQuantity = 0;
            this.ProductPrice = 0;
            this.rowsFilter2 = [];
            this.rowsFilter = [];
            this.variation_array = [];
            this.measurement_final_ids = [];
            $("#medicine-image").val('');
            $('#image-div').hide();
          } else {
            swal(response.message, "Medicine not added!", "error");
          }

        }, err => {
          var obj = JSON.parse(err._body);
          this.runnerLoader = false;
          swal(obj.message, "Medicine not added!", "error");
        });  

    }



  }//End of Add Medicine

  deleteMeasurement(id) {
    this.rowsFilter2 = this.rowsFilter2.filter(function (obj) {
      return obj.Id != id;
    })
  }

  selectedMeasurementArry: any;


  getMeasurementSelected(elem) {
    this.selectedMeasurementArry.push(elem);
    this.measurementIdArray.push(elem);
   /*  this.addMeasurement(); */
  }
  /* selectMeasurement() {
    this.selectedMeasurementArry = this.rowsFilter2;
    // console.log(this.selectedMeasurement);
  }//end of selected Measurement */


  getImageUrl(event) {
    let file = event.target.files[0];
    this._addMedicineService.addImage(file).subscribe(response => {
      this.ImageUrl = response.data;
      $('#image-div').show();
    });
  }//End of Get Image URL
  Measurement_Id: any;


  addVariation() {


 

    var error = false;

    this.variation_model = {
      Id: this.variation_array.length + 1,
      measurement_id: 0,
      measurement_name: "",
      dosage_id: 0,
      dosage_name: "",
      dosage_value_id: 0,
      dosage_value_name: "",
      quantity: 0,
      price: 0
    }



    if (this.Measurement_Id == 0 || this.Measurement_Id == undefined) {
      error = true;
      this.measurement_id_error = true;
    }
    if (this.DosageId == 0 || this.DosageId == undefined) {
      error = true;
      this.dosage_id_error = true;

    }
    if (this.DosageValueId == 0 || this.DosageValueId == undefined) {
      error = true;
      this.dosage_value_id_error = true;

    }
    if (this.ProductPrice == 0 || this.ProductPrice == undefined) {
      error = true;
      this.product_price_error = true;
    }
    if (this.ProductQuantity == 0 || this.ProductQuantity == undefined) {
      error = true;
      this.product_quantity_error = true;
    }



    if (error) {

    } else {



      var measurementObj = this.selectedMeasurementArry.find(x => x.Id == this.Measurement_Id);


      this.variation_model.measurement_id = measurementObj.Id;
      this.variation_model.measurement_name = measurementObj.MeasurementName;
      /*  this.variation_model.measurement_id = measurementObj.measurement_id;
       this.variation_model.measurement_name = measurementObj.measurement_name; */

      var dosageObj = this.dosages_array.find(x => x.Id == this.DosageId);
      this.variation_model.dosage_id = this.DosageId;
      this.variation_model.dosage_name = dosageObj.UnitName;

      var dosageObj = this.dosages_sizes_array.find(x => x.Id == this.DosageValueId);
      this.variation_model.dosage_value_id = this.DosageValueId;
      this.variation_model.dosage_value_name = dosageObj.Value;

      this.variation_model.price = this.ProductPrice;
      this.variation_model.quantity = this.ProductQuantity;
      this.variation_array.push(this.variation_model);

      this.rowsFilter = this.variation_array;
      this.tempFilter = this.variation_array;




    }


  }// End of Add Variation





  // Images//


  changeImage() {
    $("#imgInp").click();
  }

  changeimage(elem) {
    this.readURL(elem);
  }
  readURL(input) {


    var reader: any,
      target: EventTarget;
    reader = new FileReader();

    input = document.getElementById('imgInp');
    if (input.files && input.files[0]) {
      // var reader = new FileReader();
      var self = this;
      reader.onload = function (e) {
        $('#blah').attr('src', e.target.result);
        self.ProfilePicture = input.files[0];
        $("#blah").css("width", "250px");
        self.saveImageService();
      }

      reader.readAsDataURL(input.files[0]);


    }
  }


  saveImageService() {

    this._userService.saveImageForPharmacy(this.ProfilePicture).subscribe(a => {

      this.LogoUrl = a.data;

    })

  }

  //Images//
  addMeasurement() {

    var measurement_obj = this.measurements_array.find(x => x.Id == this.MeasurementListId);
    this.measurement_model = {
      Id: this.measurement_final_ids.length + 1
    }
    this.measurement_final_ids.push(this.measurement_model);
    this.rowsFilter2 = this.measurement_final_ids;
    this.tempFilter2 = this.measurement_final_ids;
  }//End of Add Measurement
  deleteVariation(Id) {
    var index = this.variation_array.findIndex(x => x.Id == Id);
    this.variation_array.splice(index, 1);
    this.rowsFilter = this.variation_array;
    this.tempFilter = this.variation_array;
  }
  updateFilter(event) {
    const val = event.target.value.toLowerCase();

    // filter our data
    const temp = this.tempFilter.filter(function (d) {
      return d.name.toLowerCase().indexOf(val) !== -1 || !val;
    });

    // update the rows
    this.rowsFilter = temp;
    // Whenever the filter changes, always go back to the first page
    this.table.offset = 0;
  }
  onPage(event) {
    clearTimeout(this.timeout);
    this.timeout = setTimeout(() => {
      console.log('paged!', event);
    }, 100);
  }
}


